import {Grid, Button, Typography} from '@material-ui/core';
import Icon from '@mdi/react'
import { mdiQrcode, mdiQrcodeScan } from '@mdi/js';
import { Link } from "react-router-dom";
import icon from './icon.png'

function Home() {

    return (
        <div>
            <img src={icon} style={{width: "20%"}} alt="Logo Claro Pay"></img>
            <Typography style={{margin:30}} variant="h2">
            Claro Pay QR Code
            </Typography>

            <Grid container spacing={12}>
                <Grid item xs={6}>
                    <Link to="/qr_generator">
                    <Button variant="contained" size="large" color="secondary">
                        <Icon
                        style={{padding:10}}
                        path={mdiQrcode}
                        title="QR Generator"
                        size={3}
                        color="white"
                        />
                    </Button>
                    </Link>
                </Grid>
                <Grid item xs={6}>
                    <Link to="/qr_scanner">
                    <Button variant="contained" size="large" color="secondary">
                        <Icon
                        style={{padding:10}}
                        path={mdiQrcodeScan}
                        title="QR Scanner"
                        size={3}
                        color="white"
                        />
                    </Button>
                    </Link>
                </Grid>
            </Grid>

        </div>
    );
  }

  export default Home;